<template>
    <div class="hangqing" v-if="indexshow">
        <headerBar :title="headerNavData"></headerBar>
        <div class="topdiv">
          <yd-pullrefresh :callback="loadList" ref="pullrefreshDemo" loading-text="" pull-text="" drop-text="">
            <yd-infinitescroll :callback="hangqing" ref="infinitescrollDemo">
              <div class="pro_list" theme="1" slot="list">
                <yd-flexbox class="pro_list_item">
                      <yd-flexbox-item class="pro_list_mt">{{$t('code.mingcheng')}} &nbsp;&nbsp;</yd-flexbox-item>
                      <yd-flexbox-item class="pro_list_mt1"><span>{{$t('code.nprice')}}(~{{coin}}) </span></yd-flexbox-item>
                      <yd-flexbox-item class="pro_list_mt2">&nbsp;&nbsp; {{$t('code.zdfp')}}</yd-flexbox-item>
                  </yd-flexbox>
                  <yd-flexbox class="pro_list_item" v-for="item in list" @click.native="gohangqingdetail(item)">
                      <yd-flexbox-item  class="pro_list_price">
                          <div  class="pro_list_img" style="float: left;">
                            <div class="imgwai"><img :src="item.currIconPath"></div>
                          </div>
                          <div class="" style="float: left;">
                            <span class="pname">{{item.currName}}</span>
                          </div>
                      </yd-flexbox-item>
                      <yd-flexbox-item  class="pro_list_pz">
                          <span>{{item.currPrice | numToFixedFilter(4)}}</span>
                      </yd-flexbox-item>
                      <yd-flexbox-item>
                        <span class="pro_list_ps" :class="{green:parseFloat(item.increase)>0,red:parseFloat(item.increase)<0,white:item.increase==null}">{{item.increase | increase}}</span>
                      </yd-flexbox-item>
                  </yd-flexbox>
              </div>
              <span slot="doneTip">{{$t('toast.nodata')}}</span>
            </yd-infinitescroll>
          </yd-pullrefresh>
        </div>
        <!-- 尾部 -->
        <footerBar :footerdata="footerdata"></footerBar>
    </div>
  
</template>

<script>
  import headerBar from './header.vue'
  import footerBar from './footer.vue'
  import {getCookie,setCookie,getCookieWithDefault} from '../assets/js/cookie.js'
export default {
  beforeCreate () {
    setTimeout(function(){
      document.querySelector('body').setAttribute('style', 'background:#ffffff')
    },10)
  },
  beforeDestroy () {
    document.querySelector('body').setAttribute('style', 'background:#ffffff')
  },
  components:{
      "headerBar":headerBar,
      "footerBar":footerBar,
  },
  data () {
    return {
      indexshow:false,
      headerNavData:{
         left:false,
         title:this.$t('header.hangqing'),
         right:false,
         bj:'#2c94ed',
         border:false
      },
      footerdata:{
        bj:'#fdfdff',
      },
      coin:getCookieWithDefault('coin','CNY'),
      navbj:0,
      navlist: [],
      page:1,
      pagesize:15,
      pagenum:0,
      list:[]
    }
  },
  created(){
    //this.navdata()
    this.hangqing()
    this.indexshow=true
  },
  methods:{
    closemune(){
       this.$parent.mune();
    },
    gohangqingdetail(item){
      this.$router.push({path:'/hangqing_detail',query:{name:item.currName}})
    },
    loadList(){
      this.page=1
      this.pagesize=15
      this.list=[]
      this.hangqing()
    },
    hangqing(){
      this.openloading(this.$t('toast.loding'))
      var that = this;
      this.axios({
        url:this.Url + 'Currency.ashx',
        method:'post',
        params: {req:5,pageindex:this.page,pagecount:this.pagesize,moneycode:this.coin}
      }).then(function (res) {
          that.closeloading()
          that.$refs.pullrefreshDemo.$emit('ydui.pullrefresh.finishLoad');
          var _list = res.data.data;
          if (res.data.data.length == 0) {
              that.$dialog.toast({
                  mes: that.$t('toast.nodata'),
                  timeout: 1500
              });
              return;
          }else{
            for (var i = 0; i < _list.length; i++) {
              _list[i].currIconPath = that.imgUrl + _list[i].currIconPath
              if (parseFloat(_list[i].increase)>0) {
                 var a = _list[i].increase.substring(0,1)
                 if (a != "+") {
                    _list[i].increase = "+" + _list[i].increase
                 }
              }
            }
            that.list = [...that.list, ..._list];
            that.pagenum = res.data.totalCount
            if (res.data.totalCount == 0) {
                /* 所有数据加载完毕 */
                that.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.loadedDone');
                return;
            }

            that.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.finishLoad');
            that.page++;
          }
      }).catch(function(res){
        that.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.finishLoad');
        that.catchErr()
        setTimeout(function(){
            that.$dialog.confirm({
                title: that.$t('data.networkerr')+'...',
                opts: (res) => {
                    that.hangqing() 
                }
            });
        },2000)
      });
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hangqing{
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
  -webkit-touch-callout:none;
  -webkit-user-select:none;
  -khtml-user-select:none;
  -moz-user-select:none;
  -ms-user-select:none;
  user-select:none;
}
.pro_list_mt{
  color: #7a8996!important;
  text-align: left;
  margin-left: .3rem;
  font-size: .28rem
}
.pro_list{
  background: #fff;
  margin-top: 0!important
}
.topdiv{
  padding-top: .9rem;
}
.pro_list_mt1{
  color: #7a8996!important;
  text-align: center;
  font-size: .28rem
}
.pro_list_mt2{
  color: #7a8996!important;
  text-align: right;
  padding-right: .3rem;
  font-size: .28rem
}
.red{
  color: #fff!important;
  background: #e86a60!important;
}
.green{
  color: #fff!important;
  background: #069f55!important;
}
.white{
  color: #888!important
}
.hangqing{
  /* padding-top: .9rem; */
  padding-bottom: 1.1rem;
}
.pro_list_price{
  text-align: left;
  /*padding-left: .2rem*/
}
.pro_list_item{
    border-bottom: 1px solid #f4f4f4;
    font-family:Arial,Tahoma,Simsun,sans-serif!important;
    /*background: #fff;*/
    padding: .2rem;
}

.pro_list_price span.pname{
    display: block;
    font-size: .35rem;
    /*font-weight: bolder;*/
    color: #384d60;
    line-height: .75rem
}
.pro_list_price span.ptype{
    display: block;
    font-size: .22rem;
    color: #666;
}
.imgwai{
  width: .6rem;
  height: .6rem;
  border-radius: 50%;
  border:.02rem solid #deebfd;
  position: relative;
  margin-right: .15rem
}
.pro_list_img img{
    width: .24rem;
    height: .24rem;
    border-radius: 50%;
    display: inline-block;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -.12rem;
    margin-left: -.12rem;
    
}
.pro_list_pz{
  text-align: center;
}
.pro_list_pz span:nth-child(1){
    font-size: .35rem;
    color: #384d60;
    display: block;
}
.pro_list_pz span:nth-child(2){
    font-size: .22rem;
    color: #666;
}
.pro_list_ps{
  font-size: .3rem;
  text-align: center;
  display: block;
  width: 1.5rem;
  height: .64rem;
  line-height: .64rem;
  margin: 0 auto;
  border-radius: 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-right: 0rem;
}
.navlist{
  position: relative;
  height: .75rem;
  font-size: .28rem!important;
}
.yd-scrollnav-tab-item>li{
  font-size: .28rem
}
.yd-scrollnav-unfold-header div{
  color: #aaa!important
}
.nav_list_item{
  background: #383838
}
.nav_list_item_list{
  overflow-x: auto;
  overflow-y: hidden; 
  white-space: nowrap;
  
}
.nav_list_item_list::-webkit-scrollbar{
  display:none;
}
.nav_list_item_list span{
  display: inline-block;
  height: .7rem;
  line-height: .7rem;
  padding: 0 .2rem;
  color: #aaa;
  font-size: .28rem;
}
.curcolor{
  color:#3e9bf0!important
}
.loaddata{
  width: 100%;
  text-align: center;
  height: 45px;
  line-height: 45px;
  color: #888;
  font-size: .26rem;
}
</style>
<style>
.yd-pullrefresh-dragtip {
    pointer-events: none;
    position: absolute;
    top: 50px!important;
    left: 50%!important;
    -webkit-transform: translate(0);
    transform: translate(0);
    width: 36px!important;
    height: 36px;
    margin-left:-18px;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    color: #6e6e6e;
    font-size: 14px;
    text-align: center;
    background-color: rgba(240,250,250,.9);
    z-index: 9999!important;
    border-radius: 50%;
}
.yd-pullrefresh-dragtip-icon>i{
  margin:0!important
}
</style>




